.App { font-family: sans-serif; }

.App, .App-header, .App-footer {
  margin: 0;
  padding: 0;
}

.App-header {
  display: flex;
  align-items: center;
  border-bottom: solid #1ed2ac;
}

.App-header h1 {
  display: inline;
  margin-left: 15px;
  color: #1ed2ac;
}

.App-logo {
  width: 200px;
  height: 200px;
}

.App-body {
  margin: 50px;
  color: white;
}

